import React, { forwardRef } from "react"
import SendMail from "./SendMail"
/* eslint-disable */
const Overlay = forwardRef(({ caption, scroll }, ref) => (
    <div
        ref={ref}
        onScroll={(e) => {
            scroll.current = e.target.scrollTop / (e.target.scrollHeight - window.innerHeight)
            caption.current.innerText = scroll.current.toFixed(3)

            /* addActive for SideBar */


            scroll.current < 0.210 ? document.getElementById('sideHome').className = "active" : document.getElementById('sideHome').className = "";

            scroll.current > 0.210 && scroll.current < 0.315 ? document.getElementById('sideCegunkrol').className = "active" : document.getElementById('sideCegunkrol').className = "";

            scroll.current > 0.315 && scroll.current < 0.421 ? document.getElementById('sideSzoftverfejlesztes').className = "active" : document.getElementById('sideSzoftverfejlesztes').className = "";

            scroll.current > 0.421 && scroll.current < 0.526 ? document.getElementById('sideWebfejlesztes').className = "active" : document.getElementById('sideWebfejlesztes').className = "";

            scroll.current > 0.526 && scroll.current < 0.631 ? document.getElementById('sideTanacsadas').className = "active" : document.getElementById('sideTanacsadas').className = "";

            scroll.current > 0.631 && scroll.current < 0.736 ? document.getElementById('sideProjektvezetes').className = "active" : document.getElementById('sideProjektvezetes').className = "";

            scroll.current > 0.736 && scroll.current < 0.841 ? document.getElementById('sideRendszerintegracio').className = "active" : document.getElementById('sideRendszerintegracio').className = "";

            scroll.current > 0.841 && scroll.current < 0.946 ? document.getElementById('sideUzemeltetes').className = "active" : document.getElementById('sideUzemeltetes').className = "";

            scroll.current > 0.946 && scroll.current < 1 ? document.getElementById('sideReferenciak').className = "active" : document.getElementById('sideReferenciak').className = "";
            /* /addActive for SideBar */

        }}
        className="scroll">
        <div id="home" style={{ height: "400vh" }}>
            <div className="dot">
                <h1>Yvorl Kft.</h1>
            </div>

            <div className="mouse_scroll">
                <SendMail />

                <div className="mouse">
                    <div className="wheel"></div>
                </div>
                <div>
                    <span className="m_scroll_arrows unu"></span>
                    <span className="m_scroll_arrows doi"></span>
                    <span className="m_scroll_arrows trei"></span>
                </div>
            </div>
        </div>
        <div id="cegunkrol" style={{ height: "200vh" }}>
            <div className="dot">
                <h1>Cgnkrl</h1>
                Magyar tulajdonban lv YVORL Informatikai Tancsad s Szolgltat Kft. 2017 ta a kis-, kzp- s nagyvllalatok informatikai szakembereinek tancsadja. Munkatrsaink fiatal, lendletes csapata mrnki, gazdasgi s informatikai tudssal s szakmai tapasztalattal is rendelkezik, gy az informatikai szaktancsads s szolgltats minden terletn szmthat cgnkre.
            </div>


        </div>
        <div id="szoftverfejlesztes" style={{ height: "200vh" }}>
            <div className="dot">
                <h1>Szoftverfejleszts</h1>Cgnk alaptevkenysge informatikai rendszerek, egyedi szoftverek, adatbzisok tervezse s megvalstsa. Forduljon hozznk bizalommal szoftverfejlesztsi ignyvel, tletvel!
            </div>
        </div>
        <div id="webfejlesztes" style={{ height: "200vh" }}>
            <div className="dot">
                <h1>Weboldal fejleszts</h1>Cgnk jelents szakmai tapasztalattal rendelkezik a webprogramozs terletn. Vllaljuk webshopok, bemutatkoz oldalak, portlok tervezst, fejlesztst, valamint egyedi tletek megvalstst.
            </div>
        </div>
        <div id="tanacsadas" style={{ height: "200vh" }}>
            <div className="dot">
                <h1>Tancsads</h1>Felmrjk a vllalat ignyeit, ennek megfelelen hozunk ltre munkacsoportokat vagy ksztnk tanulmnyt, rendszertervet az zemeltetsi, Biztonsgi, Szoftverfejlesztsi terleteken. Igny szerint megtervezzk s megalkotjuk azokat az alkalmazsokat, amelyekkel a vllalkozs folyamatai standardizlhatk, optimalizlhatak illetve automatizlhatk lesznek.

                Minden esetben az zleti dntshozk elvrsainak megfelel, hatkony hasznlatot biztost megoldsokat ajnlunk s szlltunk s az ltalunk tett javaslatokra, illetve megvalstsokra garancit is vllalunk.
            </div>
        </div>
        <div id="projektvezetes" style={{ height: "200vh" }}>
            <div className="dot">
                <h1>Projektvezets</h1>A projektmenedzsment szolgltats szigor mdszertani alapokon nyugv tevkenysg, ami lehetv teszi a feladatok sikeres elvgzst. Az alkalmazott mdszerek alapjn a megfelel projektszervezet fellltsa, a feladatok elksztse, a munkk leveznylse, a folyamatos kontrol gyakorlsa tartozik a felelssgei kz. A projekt vgrehajtsa sorn alkalmazott kontrolling s jelentsi rendszer lehetsget biztost a megrendel rszre az esetleges korrekcikra.
            </div>
        </div>
        <div id="rendszerintegracio" style={{ height: "200vh" }}>
            <div className="dot" >
                <h1>Rendszerintegrci</h1>A partnernk informatikai alapinfrastruktrjnak tervezse, optimalizlsa, illetve mkdsbeli tmogatsa. Mindez a hlzattervezstl s annak kiptstl elindulva a munkallomsok klnbz alkalmazsainak teleptsn, belltsn keresztl egszen a biztonsgos internethasznlat (tzfalak s behatolsrzkel-rendszerek) kialaktsig mindent magban foglalhat.
            </div>
        </div>
        <div id="uzemeltetes" style={{ height: "200vh" }}>
            <div className="dot">
                <h1>zemeltets s tmogats</h1>Kihelyezett rendszergazdai szolgltatsokat nyjtunk valamint karbantartjuk a vllalat informatikai infrastruktrjt kpez eszkzket, legyen az Windows, Linux vagy brmilyen UNIX alap rendszer.

                Szerverkarbantarts
                Munkallomsok karbantartsa
                Biztonsgi mentsek, archivls
                Szerverek zemeltetse
                Adatbzis-rendszerek zemeltetse
            </div>
        </div>
        <div id="referenciak" style={{ height: "200vh" }}>
            <div className="dot">
                <h1>Referencik</h1>
                TELEKOM Nyrt.
                NRG Services Kft.
                Fusiongate Kft.	DSG Services Kft
            </div>
        </div>
        <span className="caption" ref={caption}>
            0.000
        </span>
    </div>
))

export default Overlay
